          <div class="text-right">
            <div class="text-lg font-bold text-primary-600">{{ fatigueTestStore.progress }}%</div>
            <div class="text-xs text-gray-500"></div>
          </div>
        </div>

        <!--  -->
        <div class="mt-3">
          <ProgressBar
