              <div class="text-center">
                <p class="font-medium"></p>
                <p class="text-2xl font-bold ${_fatigueLevel >= 3 ? 'text-red-600' : _fatigueLevel >= 2 ? 'text-orange-600' : 'text-yellow-600'}">${_fatigueLevel >= 3 ? '' : _fatigueLevel >= 2 ? '' : ''}</p>
              </div>
            </div>

            ${_fatigueLevel >= 2 ? `
            <div class="mt-4 p-3 bg-red-100 rounded-lg">
              <p class="text-red-800"><strong> </strong></p>
            </div>
            ` : ''}
          </div>
        </div>

        <!--  -->
        <div class="bg-white p-4 rounded-lg border-l-4 border-red-200">
          <h4 class="font-semibold text-red-900 mb-3"> </h4>
          <div class="space-y-3 text-gray-700">
            <p><strong></strong>${recoveryScore}/10 </p>
            <p><strong></strong>${_recoveryLevel === 'high' ? '3-6' : _recoveryLevel === 'medium' ? '6-12' : '12-18'}</p>
            <p><strong></strong></p>

            <p><strong></strong>${_recoveryLevel === 'low' ? '' : ''}</p>
          </div>
        </div>

        <!--  -->
        <div class="bg-white p-4 rounded-lg border-l-4 border-red-200">
          <h4 class="font-semibold text-red-900 mb-3"> </h4>
          <div class="space-y-3 text-gray-700">
            <h5 class="font-medium"></h5>
            <ul class="space-y-1 text-sm">
              <li> 1-10</li>
              <li> </li>
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>

            <h5 class="font-medium"></h5>
            <ul class="space-y-1 text-sm">
              <li> </li>
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  `
}

